





























































@import "~@shop-local/shop-local-theme/scss/variables";

.box-shadow {
  box-shadow: 0 0 (min($spacer-x, $spacer-y) / 2) 0 $gray-900;
}

.block-grid {
  margin-top: -($spacer * 1.6);

  img {
    width: 100%;
  }

  .container {
    position: relative;
    top: -($spacer * 4.7);
    z-index: 2;
  }
}
